e-padding: .5rem;\n.template-body {\n  padding: 0 $h-margin $v-margin;\n  height: 100%;\n\n  .header-line {\n    display: flex;\n    padding-bottom: $line-padding;\n    padding-top: $line-padding;\n    border-bottom: $faint-separator-color 1px solid;\n  }\n\n  .line-label {\n    font-size: 1.1rem;\n    color: $faint-text-color;\n    margin-right: 1rem;\n  }\n}\n\n.content {\n  min-height: 8rem;\n  margin-top: 1rem;\n}\n\n.editable-area-layers {\n  position: relative;\n\n  .footer-stack {\n    display: inline-block;\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    z-index: 100;\n  }\n}\n\n.footer {\n  display: flex;\n  flex-direction: row;\n  align-items: flex-end;\n\n  // Stack of formatting toolbar on top of misc toolbar\n  & > :last-child {\n    margin-left: 1rem;\n    display: flex;\n    flex-direction: column;\n\n    & > :first-child {\n      margin-bottom: .5rem;\n    }\n  }\n}\n\n#bottom-row {\n  display: flex;\n  flex-direction: row;\n}\n\n// Eliminate outline on focused content-editable div\n*:focus {\n  outline: none;\n}\n\nimg {\n  max-width: 1000px;\n}\n"],"sourceRoot":""}]);
